'use client';
import React, { useState } from 'react';
import { useApp } from '@/context/AppContext';

export default function MobileHeader() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const { theme, language, toggleTheme, toggleLanguage } = useApp();

  const menuItems = [
    { name: { pt: 'In√≠cio', en: 'Home' }, href: '#home' },
    { name: { pt: 'Projetos', en: 'Projects' }, href: '#projects' },
    { name: { pt: 'Sobre', en: 'About' }, href: '#about' },
    { name: { pt: 'Habilidades', en: 'Skills' }, href: '#skills' },
    { name: { pt: 'Contato', en: 'Contact' }, href: '#contact' },
  ];

  return (
    <>
      {/* Mobile Header */}
      <header className="lg:hidden fixed top-0 left-0 right-0 bg-white dark:bg-gray-900 border-b border-gray-200 dark:border-gray-700 p-4 z-50">
        <div className="flex justify-between items-center">
          <div>
            <h1 className="text-xl font-bold text-gray-900 dark:text-white">SnapFolio</h1>
          </div>
          
          <div className="flex items-center space-x-4">
            {/* Theme Toggle Mobile */}
            <button onClick={toggleTheme} className="p-2">
              {theme === 'light' ? 'üåô' : '‚òÄÔ∏è'}
            </button>
            
            {/* Language Toggle Mobile */}
            <button onClick={toggleLanguage} className="p-2 text-sm font-medium">
              {language === 'pt' ? 'EN' : 'PT'}
            </button>

            {/* Hamburger Menu */}
            <button
              onClick={() => setIsMenuOpen(!isMenuOpen)}
              className="p-2"
            >
              <div className="w-6 h-6 flex flex-col justify-between">
                <span className={`w-full h-0.5 bg-gray-900 dark:bg-white transition-transform ${isMenuOpen ? 'rotate-45 translate-y-2' : ''}`}></span>
                <span className={`w-full h-0.5 bg-gray-900 dark:bg-white opacity-${isMenuOpen ? '0' : '100'} transition-opacity`}></span>
                <span className={`w-full h-0.5 bg-gray-900 dark:bg-white transition-transform ${isMenuOpen ? '-rotate-45 -translate-y-2' : ''}`}></span>
              </div>
            </button>
          </div>
        </div>

        {/* Mobile Menu Dropdown */}
        {isMenuOpen && (
          <nav className="absolute top-full left-0 right-0 bg-white dark:bg-gray-900 border-b border-gray-200 dark:border-gray-700">
            <ul className="py-2">
              {menuItems.map((item) => (
                <li key={item.href}>
                  <a
                    href={item.href}
                    onClick={() => setIsMenuOpen(false)}
                    className="block py-3 px-4 text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-800"
                  >
                    {item.name[language]}
                  </a>
                </li>
              ))}
            </ul>
          </nav>
        )}
      </header>

      {/* Spacer for fixed header */}
      <div className="lg:hidden h-16"></div>
    </>
  );
}